/**
 * Get plural of word
 *
 * @param value How many of the word exist
 * @param word Word wanna make plural
 * @param suffixPlural Suffix word
 *
 * @return string
 */
export const pluralize = (value = 0, word = '', suffixPlural = 'en') => {
  return value > 1 ? `${word}${suffixPlural}` : word;
};

/**
 * Get plural of exercise
 *
 * @param value How many of the exercise exist
 *
 * @return string
 */
export const pluralizeExcercise = (value = 0) => {
  return `${value} ${value === 1 ? 'Übung' : 'Übungen'}`;
};
